.form_input {
  margin: 10px 0;
}
.form_input input {
  width: 100%;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 5px 10px;
  min-height: 40px;
  outline: none;
}
.form_input button {
  background: #333;
  color: white;
  padding: 10px 0;
  width: 150px;
  margin: 20px 0;
}
